.page {
    min-width: 320px;
    width: 100%;
    min-height: 1217px;
    height: 100%;
    background-color: #000000;
    margin: 0 auto;
    padding: 0;
    font-family: 'Inter', "Arial", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
